import React,{Component} from 'react';
import {Navbar,NavbarBrand,Nav,NavbarToggler,NavItem,Collapse,Jumbotron,Button,Modal,Form,ModalBody,ModalHeader, FormGroup, Label, Input} from 'reactstrap';
import {NavLink} from 'react-router-dom';
class Header extends Component{
    
    constructor(props){
        super(props);
        this.state={
            isNavOpen :false,
            isModelOpen :false
        };
        this.toggleNav=this.toggleNav.bind(this);
        this.toggleModel=this.toggleModel.bind(this);
        this.handleLogin=this.handleLogin.bind(this);
    }

    toggleNav(){
        this.setState({
            isNavOpen :!this.state.isNavOpen
        });
    }

    toggleModel(){
        this.setState({
            isModelOpen :!this.state.isModelOpen
        });
    }

    handleLogin(event){
        this.toggleModel();
        alert("Username:" +this.username.value +"Password :" +this.password.value +"Remember :"+this.remember.checked);
        event.preventDefault();
    }

    render(){
        return(
            
        <React.Fragment>
            
            <Navbar dark expand='md' >
                <div className='container'>
                    <NavbarToggler onClick={this.toggleNav}/>
                <NavbarBrand  className='mr-auto' href="/">
                    <img src="assets/images/logo.png" height="30" width="41" alt="Restorante Con Fusion"/>
                </NavbarBrand>

                <Collapse isOpen={this.state.isNavOpen} navbar>
                <Nav navbar>
                    
                    <NavItem>
                        <NavLink className="nav-link" to="/home">
                            <span className="fa fa-home fa-lg"></span>Home
                        </NavLink>
                    </NavItem>

                    <NavItem>
                        <NavLink className="nav-link" to="/aboutus">
                            <span className="fa fa-info fa-lg"></span>About Us
                        </NavLink>
                    </NavItem>

                    <NavItem>
                        <NavLink className="nav-link" to="/menu">
                            <span className="fa fa-list fa-lg"></span>Menu
                        </NavLink>
                    </NavItem>
                    
                    <NavItem>
                        <NavLink className="nav-link" to="/contactus">
                            <span className="fa fa-address-card fa-lg"></span>Contact Us
                        </NavLink>
                    </NavItem>

                </Nav>
                <Nav className='ml-auto' navbar>
                <NavItem>
                    <Button outlint onClick={this.toggleModel}>
                        <span className='fa fa-sign-in fa-lg'> </span>Login
                    </Button>
                </NavItem>
                </Nav>
                </Collapse>
            </div>
            </Navbar>

            <Jumbotron>
                <div className='container'>
                    <div className='row row-header'>
                        <div className='col-12 col-sm-6'>
                            <h1>
                                Restorente Con Fusion
                            </h1>
                            <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                        </div>
                    </div>
                </div>
            </Jumbotron>

            <Modal isOpen={this.state.isModelOpen} toggle={this.toggleModal}>
                <ModalHeader>Login</ModalHeader>
                <ModalBody>
                    <Form onSubmit={this.handleLogin}>
                        <FormGroup>
                            <Label htmlFor="username">Username</Label>
                            <Input type="text" placeholder="Username" id='username' name='username' innerRef={(input)=>this.username=input}  />
                        </FormGroup>

                        <FormGroup>
                            <Label htmlFor="password">Password</Label>
                            <Input type="password"  placeholder="Password" id='password' name='password'  innerRef={(input)=>this.password=input}/>
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                            <Input type="checkbox" name="remember" innerRef={(input)=>this.remember=input}/>Remember Me
                            </Label>
                        </FormGroup>
                        <Button type="submit" value="submit"  color="primary">Login</Button>
                    </Form>
                </ModalBody>
            </Modal>
            
            
        </React.Fragment>
        );
    }

}

export default Header;